<template>
    <div :endTime="endTime" :callback="callback" :endText="endText" class="timers">
        <slot>
           <i>倒计时：</i>
           <span>{{day||'00'}}</span>
           <i>天</i>
           <span>{{hour||'00'}}</span>
           <i>时</i>
           <span>{{Minute||'00'}}</span>
           <i>分</i>
           <span>{{second||'00'}}</span>
           <i>秒</i>
        </slot>
    </div>
</template>
<script>
    export default {
        data(){
           return {
            day :'',
            hour: '',
            Minute:'',
            second:''
           }
        },
        props:{
            endTime:{
                type: Number || String,
                default :''
            },

            endText:{
               type : String,
               default:'已结束'
            },
            callback : {
                type : Function,
                default :''
            }
        },
        mounted () {
          this.countdowm(this.endTime,this.$store.state.nowTime);
        },
        methods: {
            countdowm(timestamp,now_Time){
                let self = this;
                let timer = setInterval(function(){
                    
                    let now = new Date(now_Time * 1000);
                    // console.log(now)
                    let endTime = new Date(timestamp * 1000);
                    let t = endTime.getTime() - now.getTime();
                    if(t>0){
                        let day = Math.floor(t/86400000);
                        let hour=Math.floor((t/3600000)%24);
                        let min=Math.floor((t/60000)%60);
                        let sec=Math.floor((t/1000)%60);
                        day = day < 10 ? "0" + day : day;
                        hour = hour < 10 ? "0" + hour : hour;
                        min = min < 10 ? "0" + min : min;
                        sec = sec < 10 ? "0" + sec : sec;
                        self.day =  `${day}`;
                        self.hour =  `${hour}`;
                        self.Minute = `${min}`;
                        self.second = `${sec}`;
                    }else{
                        clearInterval(timer);
                        self.day =  `00`;
                        self.hour =  `00`;
                        self.Minute = `00`;
                        self.second = `00`;
                        self._callback();
                    }
                     now_Time +=1; 
                },1000);
            },
            _callback(){
                if(this.callback && this.callback instanceof Function){
                    this.callback(...this);
                }
            }
        }
    }
</script>
<style lang="less" scoped>
     .timers{
        height:.5rem;
        text-align:center;
        weight:1.65rem;
        line-height:.5rem;
        color:#666;
        margin-top:-.08rem;
        i{
            font-style:normal;
            font-size:.28rem;
            display:inline-block;
            height:100%;
        }
        span{
            font-size:.28rem;
        }
    }
</style>


