<template>
    <div class="header clearfix">
        <div class="logo"><img :src="logoImg"></div>
        <div class="download-app fr" @click="app">
            <i class="download_icon"></i>
            <p>下载</p>
        </div>
        <div @click="reloadPage" class="scode fr">
            <i class="reload_icon"></i>
            <p>刷新</p>
        </div>
        <div class="logoIn fr" @click="toLogoIn" v-if="!user_name">登录</div>
    
    </div>
</template>

<script>
    import {
        Toast
    } from 'mint-ui';
    export default {
        name: 'heade',
        data() {
            return {
                logoImg: require('@/assets/logImg.png'),
                user_name: sessionStorage.getItem('user_ID')
            }
        },
        methods: {
            toLogoIn() {
                this.$router.push({
                    path: '/LogoIn'
                })
            },
            reloadPage() {
                this.$router.go(0);
            },
            app() {
                var ua = window.navigator.userAgent.toLowerCase();
                if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                    this.$store.state.show_download = true;
                } else {
                    var u = navigator.userAgent,
                        app = navigator.appVersion;
                    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
                    var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                    if (isAndroid) {
                        //这个是安卓操作系统
                        Toast('可能需要进入手机“文件管理”找到APP并安装');
                        window.open("http://www.2tianqc.com/App/2tianqc_a.apk");
                    }
                    if (isIOS) {　　　　 //这个是ios操作系统
                        Toast('请到苹果App Store搜索ShopsN');
                        window.open("https://itunes.apple.com/cn/app/storybear/id1374901365");
                    }
                }
    
    
            }
        }
    }
</script>

<style lang="less" scoped>
    .header {
        width: 100%;
        height: 1rem;
        background: #fff;
        overflow: hidden;
        position: relative;
        .logo {
            width: 2rem;
            height: .75rem;
            margin: .25rem auto;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .download-app {
            color: #A09FA4;
            position: absolute;
            text-align: center;
            top: 0;
            right: .8rem;
            height: 100%;
            width: .5rem;
            .download_icon {
                display: block;
                height: .4rem;
                background: url(../../../assets/download-app.png) center no-repeat;
                background-size: .4rem .4rem;
                margin-top: .15rem;
            }
            p {
                margin-top: .05rem;
                font-size: .2rem;
            }
        }
        .scode {
            color: #A09FA4;
            height: 100%;
            text-align: center;
            width: .5rem;
            position: absolute;
            top: 0;
            right: .2rem;
            .reload_icon {
                display: block;
                height: .4rem;
                background: url(../../../assets/reload.png) no-repeat center;
                background-size: .45rem .45rem;
                margin-top: .15rem;
            }
            p {
                margin-top: .05rem;
                font-size: .2rem;
            }
        }
        .logoIn {
            font-size: .28rem;
            color: #4f4f4f;
            line-height: .9rem;
        }
        .my {
            height: 100%;
            width: .4rem;
            padding-right: .2rem;
            background: url(../../../assets/down.jpg) center no-repeat;
            background-size: .4rem .4rem;
        }
    }
</style>


